.button {
    width: fit-content;
    background-color: transparent;
    color: $natural-20;
    border: 2px solid $natural-20;
    border-radius: 30px;
    padding: 5px 15px;
    transition: all 0.5s ease-in-out;
  
  &:hover {
    background-color: $natural-20;
    color: $natural-100;
    font-weight: 400;
  }
}