// ----------------------------------------------------------------------------------------------------------------------
// Home Page
// ---------------------------------------------------------------------------------------------------------------------- 

section {
    width: 90%;
    max-width: 1200px;
    margin-inline: auto;
    margin-block: 105px;
  }
  
  .container {
    width: 100%;
    height: auto;
    margin-inline: auto;
    
    &.featured {
        height: 100%;
        min-height: 350px;
        padding: 30px;
        margin-top: 30px;
        
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        gap: 30px;
      
        background-color: $natural-90;
        border: 1px solid $natural-80;
        border-radius: 30px;
    }
  
    &.news {
      background-color: transparent;
      justify-content: center;
      margin-inline: auto;
      margin-top: 60px;
  
      display: grid;
      grid-auto-rows: auto;
      gap: 30px;
      
      @media (max-width: 580px) {
        grid-template-columns: minmax(250px, 1fr);
      }
      @media (min-width: 580px) and (max-width: 1190px) {
        grid-template-columns: repeat(2, minmax(250px, 1fr));
      }
      @media (min-width: 1190px) {
        grid-template-columns: repeat(4, minmax(250px, 1fr));
      }
    }
  
    &.events {
      grid-template-columns: repeat(2, 1fr);
      background-color: #555;
      padding-top: 30px;
  
      & * {
        text-wrap: wrap;
      }
    
  
      h1 {
        font-size: 12px;
      }
    }
    
    a {
      text-decoration: none;
    }
  }
  
  .featured-panel {
    grid-column-start: 2; 
    grid-column-end: span 3; 
    position: relative;
    overflow: hidden;
    display: block;
  }
  
  .featured-banner {
    width: auto;
    height: 100%;
  
    &-image {
      width: 100%;
      height: 100%;
      aspect-ratio: 1;
      object-fit: cover;
      object-position: center;
      border-radius: 10px;
      box-shadow: $shadow;
    }
  }
  
  .featured-title {
    margin: 0px 0px 15px 0px;
    font-family: $headingFont;
    font-weight: 900px;
    font-size: 30px;
    color: $accent;
    text-wrap: wrap;
  }
  
  .featured-text {
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 7;
    line-clamp: 7;
    overflow: hidden;
    font-family: "Poppins";
    color: #333;
    font-weight: 300;
    font-size: 16px;
    margin-bottom: 60px;
  }
  
  .featured-link {
    position: absolute;
    bottom: 0px;
    right: 0px;
    font-size: 14px;
    padding: 10px 15px;
    font-family: $headingFont;
    font-weight: 500;
    border-radius: 20px;
    border: none;
    background-color: rgba(0, 0, 0, 0.2);
    color: #FFF;
    transition: all 0.4s ease-in-out;
  
    &:hover {
      background-color: #d99400;
    }
  }
  
  
  
 
  
  
  /* News and Blog Post Template
  /* ---------------------------------------------------------------------------------------------------------------------- */
  
  .post-container {
    min-height: 400px;
    margin-top: 30px;
    padding: 15px;
  
    p {
      font-family: $bodyFont;
      font-size: 16px;
      font-weight: 300;
      line-height: 28px;
      color: $natural-20;
      margin-bottom: 60px;
    }
  }
  
  .post-title {
    width: 100%;
    text-wrap: wrap;
    color: $natural-20;
    font-size: 50px;
    margin: 0;
    padding: 0;
  }
  
  .post-date {
    font-family: $headingFont;
    font-size: 16px;
    color: $accent;
    margin: 0;
    margin-bottom: 30px;
  }
  
  
  /* ---------------------------------------------------------------------------------------------------------------------- */
  /* Additional News Section 
  /* ---------------------------------------------------------------------------------------------------------------------- */
  
  
  .panel {
    display: grid;
    grid-template-rows: subgrid;
    grid-row: span 4;
    grid-auto-rows: auto;
    gap: 0px;
    overflow: hidden;
    height: fit-content;
    width: minmax(275px, 300px);
    background-color: $natural-100;
    border-radius: 5px;
    border-bottom: 3px solid $natural-90;
    transition: all 0.5s ease-in-out;
    // padding-bottom: 20px;
    // margin-bottom: 20px;
  
    &:hover {
      transform: scale(1.05);
    }
  }
  
  
  .news-banner {
    aspect-ratio: 1;
    width: 100%;
    overflow: hidden;
    border-radius: 10px;
    box-shadow: $shadow;
  }
  
  .news-title {
    font-size: 15px;
    margin: 15px 15px;
    align-self: center;
    text-wrap: balance;
  }
  
  .news-text {
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 5;
    line-clamp: 5;
    overflow: hidden;
    font-family: $bodyFont;
    font-size: 12px;
    color: #333;
    text-wrap: wrap;
    margin: 0px 15px;
  }
  
  .panel span {
    justify-self: right;
    margin: 10px 15px;
    font-family: $headingFont;
    font-size: 14px;
    font-weight: 300;
    color:#d99400;
    opacity: 0;
    transition: all 0.4s ease-in-out;
  }
  
  .panel:hover span {
    opacity: 1;
  }
  
  
  /* Events Section
  /* ---------------------------------------------------------------------------------------------------------------------- */
   
  .vertical {
    display: block;
    gap: 12px;
    width: 100%;
  }
  
  .vertical p {
    color: #333;
    font-family: $bodyFont;
  }
  
  .tab {
    height: 60px;
    width: 100%;
    margin: 5px 0px;
    background-color: #333;
    grid-column: 1;
    border-radius: 5px;
  }