@import url('https://fonts.googleapis.com/css2?family=Nunito+Sans:ital,opsz,wght@0,6..12,200..1000;1,6..12,200..1000&family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');

$darkTone: #333;
$midTone: #CCC;
$whiteTone: #FFF;
$blackTone: #000;
$accent: #d99400;
$darkAccent: #C07A00;
$bodyFont: 'Poppins';
$headingFont: 'Nunito Sans';


/* Aural Glossary
/* ---------------------------------------------------------------------------------------------------------------------- */

$sidebar-width: 300px;

.glossary-container {
    width: calc(100% - $sidebar-width);
    height: 100%;
    padding: 30px;
    padding-top: 105px;  
    margin-left: 300px;
}

.glossary-navigation {
    width: $sidebar-width;
    height: 100%;
    position: fixed;
    top: 75px;
    left: 15px;
    background-color: #FFF;
    border-right: 1px solid #EEE;
}

.glossary-navigation ul {
    list-style: none;
    text-decoration: none;
    padding: 4px 0px;
}

.glossary-navigation ul > li {
    color: #000;
}

.glossary-navItem {
    font-family: $bodyFont;
    font-size: 12px;
    color: #333;
    text-wrap: nowrap;
}

// #perceptual, 
// #perceptual * {
//     color: red;
// }

// #conceptual,
// #conceptual * {
//     color: blue;
// }

.active-path {
    color: #d99400;
    text-decoration: line-through;
}

.navItem-highlight {
    color: $glossary-20;
    font-weight: 600;
}

.level-2 {
    margin-left: 10px;
}

.level-3 {
    margin-left: 20px;
}

.level-4 {
    margin-left: 30px;
}

.level-5 {
    margin-left: 40px;
}

.level-6 {
    margin-left: 50px;
}



// Glossary Content
// --------------------------------------------------------------------------------------------

.glossary-entry {
    width: 100%;
    height: fit-content;
    display: grid;
    grid-template-columns: 15px 175px 1fr;
    grid-template-rows: 50px repeat(2, auto);
    font-family: $bodyFont;
    font-size: 13px;
}

.glossary-entry > h2 {
    grid-column: 1;
    grid-row: 1;
    color: $glossary-20;
}

.glossary-entry-citation {
    grid-column: 2;
    grid-row: 2;
}

.glossary-entry-content {
    grid-column: 3;
    grid-row: 2;

    p {
        margin-bottom: 25px;
    }
}

.entry-feature-type {
    grid-column: 2;
    grid-row: 3;
}

.entry-title {
    font-weight: 600;
    color: #333;
}

.entry-feature-scale {
    grid-column: 3;
    grid-row: 3;
    display: grid;
    grid-template-columns: 90px 1fr;
    text-wrap: balance;

    &:first-child {
        grid-column: 1;
    }

    &:nth-child(2) {
        grid-column: 2;
    }
}

.entry-scale-highlight {
    padding-block: 2px;
    padding-inline: 5px;
    margin-inline: 2px;
    line-height: 35px;
    border-radius: 5PX;
    background-color: #EEE;
    color: #700000;
    text-wrap: nowrap;
}