
$shadow: 0 2px 5px rgba(0, 0, 0, 0.35); 
$accent-shadow: 0 2px 5px rgba(217, 148, 0, 0.65); 
$bodyFont: 'Poppins';
$headingFont: 'Nunito Sans';


/* Global Behaviors
/* ---------------------------------------------------------------------------------------------------------------------- */

html,
html * {
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
  scroll-behavior: smooth;

  .no-scroll {
    overflow: hidden;
  }
}

body {
  background: $natural-100;
  width: 100%;
  overflow-x: hidden;
  min-height: 100vh;

  .no-scroll {
    overflow: hidden;
  }
}

.buffer {
  height: calc(100vh + 50px);
  width: 100%;
}


.games-container {
  height: fit-content;
}
