@import url('https://fonts.googleapis.com/css2?family=Nunito+Sans:ital,opsz,wght@0,6..12,200..1000;1,6..12,200..1000&family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Source+Code+Pro:ital,wght@0,200..900;1,200..900&display=swap');

$darkTone: #333;
$midTone: #CCC;
$whiteTone: #FFF;
$blackTone: #000;
$accent: #d99400;
$darkAccent: #C07A00;
$bodyFont: 'Poppins';
$headingFont: 'Nunito Sans';


/* General Layout
/* ---------------------------------------------------------------------------------------------------------------------- */

.piece {
  
  .piece-container {
    width: 100%;
    max-width: 1200px;
    margin-inline: auto;
    column-gap: 60px;
    display: grid;
    grid-template-columns: 1fr 350px;
    column-rule: solid 20px #333; 
    // border-top: 4px solid #555;

    .piece-title {
      color: #555;
      margin-block: 20px;
  
      @media (min-width: 960px) {
        font-size: 60px;
      }
      @media (min-width: 600px) and (max-width: 960px) {
        font-size: 45px;
        margin-block: 15px;
      }
      @media (max-width: 600px) {
        font-size: 34px;
        margin-block: 15px;
      }
    }
 
    h2 {
      color: #555;
      font-weight: 900;
      padding-block: 20px;
    }
    
    @media (max-width: 960px) {
      grid-template-columns: 1fr;
    }
  }

  .left-block {
    grid-column: 1;
    width: 100%;

    p {
      font-family: $bodyFont;
      font-size: 16px;
      font-weight: 200;
      color: $darkTone;
      text-wrap: balance;
    }
  }

  .info-panel {
    // border-top:2px solid #555;
    width: 100%;
  }

  .piece-info-block {
    border-top: 3px solid #555;
    border-bottom: 1px solid #555;
    margin-block: 15px;
    grid-column: 1 / -1;
    display: grid;
    width: 100%;
    grid-template-columns: repeat(5, 1fr);
    // margin-bottom: 45px;

    .info-block-panel {
      margin-block: 10px;
      padding-block: 10px;

      &:not(:nth-child(1)) {
        border-left: 1px solid #ddd;
        padding-left: 15px;
      }
    }

    .info-block-title {
      font-family: $headingFont;
      font-size: 16px;
      font-weight: 700;
      color: $accent;
      grid-column: 1;
    }

    .info-block-data {
      font-family: $bodyFont;
      font-size: 14px;
      font-weight: 300;
      color: #555;
      grid-column: 2;
    }

    @media (max-width: 960px) {
      grid-template-columns: repeat(3, 1fr);
    }

    @media (max-width: 600px) {
      grid-template-columns: repeat(1, 1fr);
    }
  }
  
  .right-block {
    grid-column: 2;
    max-width: 340px;
    margin-inline: auto;

  
    @media (max-width: 960px) {
      grid-column: 1;
      max-width: 100%;
    }
  }
}




p .breadcrumbs {
  color: #d99400;
}

/* Awards and Performance History Lists
/* ---------------------------------------------------------------------------------------------------------------------- */


.event-card {
    /*background-color: #EEE; 
    border-radius: 5px; 
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.3); */
    width: 100%;
    min-width: fit-content;
    height: auto;
    margin-bottom: 30px;
    display: flex;
    gap: 0;
    align-content: center;
    justify-content: center;
  }
  
  .date-box {
    background-color: #555;
    border-radius: 5px;
    width: 40px;
    padding: 5px;
    min-height: 100%;
    max-height: 100%;
  }
  
  .date-box p {
    color: $midTone;
    font-family: $headingFont;
    text-align: center;
    font-weight: 800;
    font-size: 12px;
    height: 100%;
  }
  
  .location-box,
  .event-box {
    width: auto;
    white-space: nowrap;
    font-family: $bodyFont;
    font-size: 12px;
    font-weight: 300;
    color: $darkTone;
    margin-left: 10px;
  }
  
  .event-box {
    flex: 1;
  }



/* Dance Specific Elements
/* ---------------------------------------------------------------------------------------------------------------------- */

.dance-container {
  width: calc(100vw - 150px);
  height: calc(100vh - 150px);
  position: relative;
  // margin: auto;
  left: 75px;
  top: 75px;
  background-size: cover;
  border-radius: 30px;
  background-position: center;
  // background-color: #000;
}

.dance-video {
  top: 0; 
  left: 0; 
  width: 100%; 
  height: 100%;
  object-fit: cover; 
  border-radius: 30px;
}

.dance-title {
  position: absolute;
  bottom: 30px;
  left: 60px;
  color: #FFF;
}