@import url('https://fonts.googleapis.com/css2?family=Nunito+Sans:ital,opsz,wght@0,6..12,200..1000;1,6..12,200..1000&family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');

$darkTone: #333;
$midTone: #CCC;
$whiteTone: #FFF;
$blackTone: #000;
$accent: #d99400;
$darkAccent: #C07A00;
$bodyFont: 'Poppins';
$headingFont: 'Nunito Sans';
$shadow: 0 2px 5px rgba(0, 0, 0, 0.35); 

/* Z1 - Global
/* ---------------------------------------------------------------------------------------------------------------------- */

.breadcrumbs {
    font-family: $bodyFont;
    font-size: 12px;
    color: #555;
    margin-bottom: 5px;
}

img.logo {
    position: fixed;
    z-index: 7;
    height: 70px;
    width: 70px;
    top: 5px;
    left: 5px;
}

.long-name {
    position: fixed;
    z-index: 7;
    width: 175px;
    top: 26px;
    left: 85px;
    opacity: 0;
    visibility: hidden;
    transition: all 400ms ease-in-out;
}

.show {
    opacity: 1;
    visibility: visible;
    transform: translateY(-10px);
}

header.page .long-name {
    position: fixed;
    z-index: 7;
    width: 175px;
    top: 18px;
    left: 85px;
    opacity: 1;
    visibility: visible;
}



/* Z2 - Hero Image
/* ---------------------------------------------------------------------------------------------------------------------- */


.hero-container {
    position: absolute;
    margin: auto;
    z-index: 2;
    background-image: url("/assets/img/hero.png");
    background-size: cover;
    background-position: center;

    @media (min-width: 600px) {
        top: 75px;
        left: 75px;
        width: calc(100vw - 150px);
        height: calc(100vh - 150px);
        border-radius: 5px;
    }

    @media (max-width: 600px) {
        top: 0px;
        left: 0px;
        width: 100%;
        height: calc(100vh - 75px);
        border-radius: 0px;
    }
}

.banner-container {
    width: calc(100vw - 150px);
    height: calc(75vh - 150px);
    margin: auto;
    margin-top: 75px;
    z-index: 2;
    background-size: cover;
    border-radius: 5px;
    background-position: center;
    box-shadow: $shadow;
}

.hero-name {
    z-index: 3;
    position: absolute;
    width: 600px;
    bottom: -20px;
    left: 40px;
}

.composer-titles {
    font-family: $bodyFont;
    font-size: 16px;
    font-weight: 200;
    color: #333;
    position: absolute;
    bottom: -50px;
    left: 0px;

    @media (max-width: 600px) {
        width: 100%;
        text-align: center;
    }
}


/* Z3 - Desktop - MegaMenu Navigation
/* ---------------------------------------------------------------------------------------------------------------------- */

.megamenu-container,
.megamenu-container * {
    box-sizing: border-box;
}

.megamenu-container {
    position: relative;
    width: 100%;

    @media (max-width: 1159px) {
        display: none;
    }
}

.megamenu {
    position: fixed;
    width: 100%;
    top: 0;
    left: 0;
    right: 0;
    z-index: 6;
    height: 75px;
    padding: 0px;
    margin: 0px;
    display: inline-flex;
    background-color: #FFF;
}

.megamenu > ul {
    width: 100%;
    align-items: center;
    list-style-type: none;
    display: flex;
    justify-content: flex-end;
    padding: auto 0px auto 0px;
}

.megamenu > ul > li {
    height: 100%;
    padding: 0px 15px;
    align-content: center;
    list-style: none;
}

.megamenu > ul > li:hover .nav-item {
    color: $darkAccent;
    border-bottom: 1.5px solid $accent;
}

.nav-item, 
.CTA {
    text-decoration: none;
    justify-content: center;
    align-items: center;
    align-content: center;
    text-align: center;
    font-family: $headingFont; 
    font-weight: 300;
    font-size: 18px;
    line-height: 15px;
    border-bottom: none;
    transition: all 0.4s ease-in-out;
    color: #333;
}

.CTA {
    background-color: #333;
    color: #CCC;
    margin: 0px 15px;
    // margin-right: 75px;
    padding: 8px 40px;
    border-radius: 30px;
}

.CTA:hover {
    color: #FFF;
    font-weight: 500;
    box-shadow: $shadow;
}

.nav-item img {
    height: 15px;
    margin: 0px 5px;
}


.submenu-container .submenu-content {
    display: flex;
    gap: 32px;
}

.submenu {
    justify-content: center;
}


.megamenu-container .submenu-container {
    opacity: 0;
    position: absolute;
    pointer-events: none;
    left: 0;
    top: 75px;
    width: 100%;
    height: 150px;
    background-color: rgba(238, 238, 238, 0.9);
    backdrop-filter: blur(5px);
}

.megamenu-container .is-submenu:hover .submenu-container {
    opacity: 1;
    pointer-events: auto;
}

.submenu {
    height: 100%;
    width: 100%;
}

.submenu-content {
    height: 100%;
    margin: 0px auto;
    justify-content: center;
}

.submenu-column {
    height: 100%;
    width: 225px;
    align-content: top;
    padding: 20px 0;
}

.submenu-column h3 {
    font-size: 22px;
}

.submenu-column ul li {
    list-style: none;
    padding: 10px 15p;
    border-left: 1px solid #333;
    transition: all 0.25s ease-in-out;
    margin: 10px 0px;
}

.submenu-column ul li:hover {
    border-left: 3px solid $accent;
}

.submenu-column ul li a {
    text-decoration: none;
    font-family: $bodyFont;
    font-size: 16px;
    font-weight: 300;
    color: #333;
    padding: 0px 10px;
    transition: all 0.25s ease-in-out;
}

.submenu-column ul li:hover a {
    color: #d99400;
}







/* Z3 - Social Nav
/* ---------------------------------------------------------------------------------------------------------------------- */

.social-nav {
    // background-color: #000;
    width: 75px;
    position: fixed;
    top: 75px;
    left: 0px;
    z-index: 7;
    align-content: center;
    justify-items: center;
    display: grid;

    @media (min-width: 0px) and (max-width: 1349px) {
        display: none;
    }
}

.social-icon {
    width: 30px;
    margin-bottom: 10px;
}

.social-icon svg path {
    fill: rgba(0,0,0,0.2);
    transition: fill 400ms ease-in-out
}

.social-icon:hover svg path {
    fill: #d99400;
}



/* Z5 - Mobile Navigation
/* ---------------------------------------------------------------------------------------------------------------------- */

.mobile-nav {
    position: fixed;
    z-index: 5;
    width: 100%;
    height: 75px;
    top: 0;
    left: 0;
    display: none;
    background-color: #FFF;

    @media (max-width: 1159px) {
        display: block;
    }

    .hamburger {
        position: absolute;
        top: 15px;
        right: 15px;
        width: 45px;
        height: 45px;
        background: none;
        background-image: url("/assets/img/icons/menu-333.svg");
        background-size: contain;
        background-repeat: no-repeat; 
        background-position: center; 
        border: none;
        cursor: pointer;
    }
}

.mobile-nav-window {
    position: fixed;
    z-index: 8;
    top: 0px;
    width: 100vw;
    height: 110vh;
    background-color: #333;
    opacity: 0;
    visibility: hidden;
    transition: all 0.5s ease-in-out;

    ul {
        margin: 100px auto 0px auto;
        width: fit-content;
        height: fit-content;
    }

    li {
        list-style: none;
        margin: 15px 0px;
        text-align: left;
    }

    a {
        text-decoration: none;
        font-family: $bodyFont;
        font-size: 20px;
        font-weight: 400;
        color: #FFF;

        &:hover {
            color: #d99400;
        }
    }
}

.top-bar {
    height: 75px;
    width: 100%;

    .submenu-textlogo {
        width: 200px;
        margin-inline: auto;
        margin-top: 20px;
    }
}


.mobile-submenu {
    margin-top: 15px;
    margin-left: 30px;

    ul {
        margin-top: 0px;
    }

    a {
        font-family: $bodyFont;
        font-size: 14px;
        font-weight: 300;
        color: #FFF;

        &:hover {
            color: #d99400;
        }
    }
}


.mobile-nav-window.show {
    opacity: 1;
    visibility: visible;
}

.mobile-nav-close {
    position: absolute;
    top: 25px;
    right: 15px;
    width: 45px;
    height: 45px;
    background: none;
    background-image: url("/assets/img/icons/close-CCC.svg");
    background-size: contain;
    background-repeat: no-repeat; 
    background-position: center; 
    border: none;
    cursor: pointer;
}

/* Z5 - Listening Window
/* ---------------------------------------------------------------------------------------------------------------------- */

.listenWindow {
    position: fixed;
    z-index: 7;
    width: 100%;
    height: 100%; 
    top: 0;
    left: 0;
    background-color: rgba(51, 51, 51, 0.9);
    backdrop-filter: blur(80px);
    opacity: 0;
    visibility: hidden;
    transition: all 0.5s ease-in-out;
}

.listenWindow.show {
    opacity: 0.95;
    visibility: visible;
}

.newButton {
    position: fixed;
    top: 150px;
    left: 150px;
    background-color: #CCC;
}


