@import url('https://fonts.googleapis.com/css2?family=Nunito+Sans:ital,opsz,wght@0,6..12,200..1000;1,6..12,200..1000&family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Source+Code+Pro:ital,wght@0,200..900;1,200..900&display=swap');

$darkTone: #333;
$midTone: #CCC;
$whiteTone: #FFF;
$blackTone: #000;
$accent: #d99400;
$darkAccent: #C07A00;
$bodyFont: 'Poppins';
$headingFont: 'Nunito Sans';


/* General Layout
/* ---------------------------------------------------------------------------------------------------------------------- */

footer {
  width: 100%;
  background-color: #333;
  margin-top: 60px;
  min-height: 250px;
  max-height: 600px;
}

.upper-footer {
  width: 100%;
  max-width: 1200px;
  min-height: 200px;
  margin: 0px auto;
  display: grid;
  grid-template-columns: repeat(1, 1fr);

  @media (min-width: 960px) {
    grid-template-columns: repeat(2, 1fr);
  }
}

.upper-footer .footer-left {
  width: 100%;
  align-self: center;
  grid-column: 1;
  margin-left: 30px;
  

  @media (max-width: 960px) {
    width: 100%;
    margin: 15px 0px;
    text-align: center;
    justify-self: center;

    .footer-logo {
      margin: 0 auto;
    }
  }

  h3 {
    color: #CCC;
    font-weight: 500;
    font-size: 14px;
  }

  .footer-logo {
    width: 175px;
    padding: 10px 0px;
  }
}


.upper-footer .footer-right {
  width: 100%;
  align-content: center;
  margin-top: 15px;
  padding: 15px;
  grid-column: 2;
  display: grid;
  grid-template-columns: auto auto auto;

  @media (min-width: 960px) {
    border-left: 1px solid #555;
  }

  @media (max-width: 960px) {
    border-top: 1px solid #555;
    grid-column: 1;
    width: 100%;
    padding-inline: 5%;
    // grid-template-columns: 20% 40;

    // & .general {
    //   grid-column: 1;
    // }

    // & .resources {
    //   grid-column: 2;
    //   grid-row: 1;
    // }

    // & .projects {
    //   grid-column: 2;
    //   grid-row: 2;
    // }
    

    .footer-bold {
      padding: 0px;
      font-size: 12px;
    }

    .footer-col .footer-reg {
      padding-left: 20px;
      font-size: 10px;
    }
  }
}

.footer-right .footer-col {
  padding-left: 15px;
  align-items: center;
  width: auto;
}

.footer-col .footer-bold {
  padding: 2px 0px;
  padding-left: 10px;
  font-weight: 800;
  font-size: 15px;
}

.footer-col .footer-reg {
  padding: 2px 0px;
  padding-left: 30px;
  font-size: 14px;
  font-weight: 300;
}

.footer-col a {
  font-family: $headingFont;
  text-decoration: none;
  color: #CCC;
  transition: all 400ms ease-in-out;
}

.footer-col a:hover {
  color: #D99400;
}


// Lower Footer --------------------------------------------------------------------------------------------
// ---------------------------------------------------------------------------------------------------------
  

.lower-footer {
  max-width: 1200px;
  width: 100%;
  min-height: 50px;
  border-top: 1px solid #555;
  margin: 0px auto;
  padding-block: 5px;
  background-color: #333;
  display: grid;
  grid-template-columns: 1fr 160px;

  .copyright {
    font-family: $headingFont;
    font-size: 12px;
    font-weight: 300;
    color: #CCC;
    grid-column: 1;
    align-self: center;
    padding-left: 30px;
  }

  .footer-socials {
    width: 100%;
    height: 100%;
    right: 0px;
    margin-block: auto;
    padding-right: 30px;
    display: inline-flex;
    gap: 15px;
    align-items: center;

    .footer-icon {
      height: 25px;
      width: 25px;
    }
    
    .footer-icon svg path {
      fill: #CCC;
      transition: fill 400ms ease-in-out;
    }
    
    .footer-icon:hover svg path {
      fill: #d99400;
    }
  }

  @media (max-width: 960px) {
    grid-template-columns: 1fr;
    align-items: center;
    align-content: center;
    text-align: center;
    padding-bottom: 20px;

    .copyright {
      margin-inline: auto;
      margin-block: 5px;
      font-size: 10px;
      margin: 15px 0px;
      text-align: center;
      padding-inline: 0px;
    }

    .footer-socials {
      margin: 0px auto 15px auto;
      justify-content: center;
      padding-inline: 0px;
    }
  }
}







