@import url('https://fonts.googleapis.com/css2?family=Nunito+Sans:ital,opsz,wght@0,6..12,200..1000;1,6..12,200..1000&family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Source+Code+Pro:ital,wght@0,200..900;1,200..900&display=swap');

$bodyFont: 'Poppins';
$headingFont: 'Nunito Sans';

// GLOBALS -----------------------------------------------------------------------------------------------------
// -------------------------------------------------------------------------------------------------------------

h1, h2, h3 {
    font-family: $headingFont;
    font-weight: 900;
    white-space: nowrap;
    text-wrap: balance;
  }
  
  h1 {
    color: $natural-20;
    
    @media (max-width: 580px) { font-size: 35px } // Mobile
    @media (min-width: 580px) { font-size: 60px } // Desktop
  }
  
  h2 {
    color: $accent;
  
    @media (max-width: 580px) { font-size: 22px } // Mobile
    @media (min-width: 580px) { font-size: 30px } // Desktop
  }
  
  h3 {
    color: $accent;
  
    @media (max-width: 580px) { font-size: 16px } // Mobile
    @media (min-width: 580px) { font-size: 20px } // Desktop
  }


// General Typography Abstractions -----------------------------------------------------------------------------
// -------------------------------------------------------------------------------------------------------------


  .quote {
    font-family: $bodyFont;
    font-size: 14px;
    font-weight: 300;
    font-style: italic;
    color: $natural-30;

    background-color: $natural-90;
    padding: 30px 30px 45px 30px;
    margin-bottom: 45px;
    border-radius: 3px;
  }
  
  .citation {
    color: $natural-20;
    font-family: $headingFont;
    font-weight: 900;
    font-size: 14px;
    float: right;
  }
  
  span.highlight {
    color: $accent-30;
    text-wrap: nowrap;

    background-color: $natural-90; 
    border-radius: 5px; 
    box-shadow: 0 1px 1px rgba(0, 0, 0, 0.3); 
    padding: 0px 5px;
  }

  a.highlight {
    text-decoration: none;
  }


// Simple Transforms -------------------------------------------------------------------------------------------
// -------------------------------------------------------------------------------------------------------------

  
  .center {
    text-align: center;
  }