.music {
    width: 95%;
    max-width: 1200px;
    margin: 100px auto 25px auto;

    .page-heading {
        padding-left: 40px;
        font-size: 50px;
        margin-bottom: 15px;
    }

    @media (max-width: 960px) {
        .page-heading {
            font-size: 40px;
        }
    }

    .in-progress-label {
        width: 100%;
        font-family: $bodyFont;
        font-size: 16px;
        font-weight: 200;
        text-align: right;
        align-self: center;
        margin-bottom: 20px; 

        img {
            vertical-align: middle;
        }
    }

    .in-progress {
        height: 20px;  
        margin: auto 0px; 
        vertical-align: bottom; 
        fill: #333333;
    }   
}

.catalog-container {
    display: block;
    width: 100%;
    margin-inline: auto;
    font-family: $bodyFont;
    margin-bottom: 105px;
}

.genre-container {
    display: grid;
    grid-template-columns: 40px 1fr;
    width: 100%;
    margin-bottom: 30px;

    .genre-label {
        flex: 0 0 auto;  
        width: 40px;
        padding-top: 5px;
    
        h2 {
            width: auto;
            align-content: top;
            transform: rotate(90deg);
        }
    }
    
    .genre-works {
        flex: 1 1 auto;  
        width: 100%;
    }

    .table-heading,
    .table-heading * {
        background-color: #333 ;
        color: #FFF !important;
        font-size: 16px !important;
        font-weight: 300!important;
        font-style: normal !important;
        border: none !important;

        @media (max-width: 960px) {
            font-size: 12px !important;
            font-weight: 300!important;
            margin: 0 !important;
        }
    }
}

.off-color {
    background-color: #eee;
}

.piece-row {
    display: grid;
    grid-template-columns: 35% 1fr 10% 5%;

    width: 100%;
    padding: 10px 15px 10px 15px;

    border-left: 2px solid #d99400;
    border-top: .5px solid #DDD;
    border-bottom: .5px solid #DDD;
    
    @media (hover: hover) and (pointer: fine) {
        &:hover,
        &:hover * { 
            background-color: #333;
            color: #FFF !important; 
        }
    }

    p {
        font-family: $bodyFont;
        font-size: 16px;
        font-weight: 200;
        color: #333;
        align-self: center;
    }

    .piece-title {
        font-weight: 500;
    }

    .piece-instr {
        font-style: italic;
    }

    .piece-dur {
        text-align: center;
    }

    .piece-year {
        text-align: center;
        font-weight: 600;
        color: #D99400;
    }

    @media (max-width: 960px) {
        width: 100%;
        grid-template-columns: 85% 15%;
        grid-template-rows: 1fr 1fr;

        .piece-title {
            grid-column: 1;
            grid-row: 1;
            font-size: 14px;
        }

        .piece-instr {
            grid-column: 1;
            grid-row: 2;
            font-size: 11px;
            margin-top: 5px;
        }

        .piece-year {
            grid-column: 2;
            grid-row: 1;
            text-align: right;
            font-size: 14px;
        }

        .piece-dur {
            grid-column: 2;
            grid-row: 2;
            text-align: right;
            font-size: 12px;
            margin-top: 3px;
        }
    }
}















/* Music Catalog Table Font Stylings
/* ---------------------------------------------------------------------------------------------------------------------- */



.channel-highlight {
    background-color: #CCC; 
    border-radius: 5px; 
    box-shadow: 0 1px 1px rgba(0, 0, 0, 0.3); 
    margin: 0px 1px;
    padding: 1px 5px;
    font-weight: 300;
    font-style: normal;
    color: #333;
}





/* Discog Styling
/* ---------------------------------------------------------------------------------------------------------------------- */
  
.discog {
    // justify-content: center;

    a {
    text-decoration: none;
    }
}

.discog-container {
    width: 100%;
    height: auto;
    margin-bottom: 60px;
    display: grid;
    grid-auto-rows: auto;
    gap: 30px;

    @media (max-width: 580px) {
        grid-template-columns: repeat(auto-fit, minmax(170px, 1fr));
    }
    @media (min-width: 580px) {
        grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
    }
    

    h2 {
        margin-top: 15px;
        grid-column: 1 / -1;
        width: auto;
    }
}

.discog-panel {
    height: fit-content;
    width: auto;
    background-color: #FDFDFD;
    transition: all 0.4s ease-in-out;
    
    display: grid;
    grid-template-rows: subgrid;
    grid-row: span 3;
    grid-auto-rows: fit-content;
    gap: 0px;
}

.discog-panel:hover {
    transform: scale(1.05);
    cursor: pointer;

    .discog-banner {
        box-shadow: $accent-shadow;
    }
  }
  
.discog-banner {
    width: 100%;
    aspect-ratio: 1;
    box-shadow: $shadow;
    transition: all 0.4s ease-in-out;
}

.discog-title {
    font-family: $headingFont;
    color: #333;
    width: fit-content;
    font-size: 16px;
    margin: 10px 5px;
    text-wrap: balance;
}

.discog-text {
    font-family: $bodyFont;
    font-size: 14px;
    color: #333;
    margin: 0px 5px;
}

