/* About Section
/* ---------------------------------------------------------------------------------------------------------------------- */

.about-container {
  display: grid;
  column-gap: 60px;
  width: 100%;
  grid-template-columns: 50% 50%;
  margin-top: 60px;

  h1 {
    vertical-align: top;
    margin-bottom: 30px;
    grid-row: 1 / -1;
  }

  .left-block, 
  .right-block {
    width: 100%;
    margin-bottom: 60px;
    grid-row: 2;
  }

  .left-block {
    grid-column: 1;

    .standard {
      font-family: $bodyFont;
      font-size: 18px;
      font-weight: 300;
      line-height: 32px;
      width: 95%;
      margin-bottom: 30px;
      text-wrap: balance;
    }

    .about-buttons {
      width: 100%;
      display: grid;
      gap: 15px;
      margin-inline: auto;
      margin-top: 60px;
      // align-content: center;
      justify-content: center;
      grid-template-columns: repeat(auto-fit, minmax(100px, 130px));

      .button {
        font-family: $headingFont;
        font-size: 18px;
        font-weight: 600;
        text-wrap: balance;
        color: $whiteTone;
        background-color: $midTone;
        width: 100%;
        aspect-ratio: 1;
        border: none;

        &:hover {
          background-color: $accent;
        }
      }

      @media (max-width: 630px) {
        grid-template-columns: repeat(2, minmax(100px, 130px));
        gap: 45px
      }
    }
  }

  .right-block {
    img.bio-photo {
      width: 100%;
      float: right;
      height: auto;
    }

    .photo-credit {
      font-family: $bodyFont;
      font-size: 14px;
      font-weight: 200;
      margin-top: 5px;
      margin-right: 0px;
      float: right;
    }
  }

  @media (max-width: 960px) {
    grid-template-columns: 1fr;

    .right-block {
      grid-row: 1;
    }

    h1 {
      grid-row: 2;
    }

    .left-block {
      grid-row: 3;
    }
  }
}

#about-container {
  border-radius: 30px;
  background-color: #f8f8f8;
}

/* Extras
/* ---------------------------------------------------------------------------------------------------------------------- */



.CV {
  width: 1200px;
  height: auto;
  margin: 100px auto;
}